





































































































































































import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";
import comhead from "../../components/Comhead.vue";

@Component({
  components: {
    comhead
  },
  mixins: [mixin]
})
export default class my_reward extends Vue {
    pageindex = 0;
    loading = false;
    award="";
    awardList=[];
    cardlist=[];
    isruleshow=false;
    isextractshow=false;
    bankcard="";
    amount="";
    card_Id="";
    awardBalance="";
    isselectshow=false;
    addshow=false;
  cardId="";
  bankName="";
  realName="";
  subBranch="";

    ruleshow(){
      this.isruleshow=!this.isruleshow
    }
    extractshow(){
      this.isextractshow=!this.isextractshow;
    }
    selectshow(){
      this.isselectshow=true;
      this.getBankCardList()
    }
    addbangcard(){
      this.extractshow();
      this.addshow=true;
    }
  addcancel(){
    this.addshow=false;
    this.cardId="";    
    this.bankName="";    
    this.realName="";    
    this.subBranch="";    
  }
    getdefaultbankcard(){
    Vue.prototype.$reqFormPost("/bank/card/getdefault", {
      userId: this.$store.getters[
            Vue.prototype.MutationTreeType.TOKEN_INFO
        ].userId,
        token: this.$store.getters[
            Vue.prototype.MutationTreeType.TOKEN_INFO
        ].token,
     }, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message)
        return;
      }
      console.log("getdefault",res.data.data);
      if(res.data.data){
        this.bankcard = res.data.data
        this.card_Id=res.data.data.id
      }      
      return
    });
  }
  getBankCardList() {
    Vue.prototype.$reqFormPost(
      "/bank/card/list",
      {
        userId: this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].userId,
        token: this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].token,
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        this.cardlist = res.data.data;
        console.log(res.data.data);
      }
    );
  }
  selectcard(item){
    this.bankcard=item;
    this.card_Id=item.id;
    this.isselectshow=false;
  }
  addcard() {
    if (this.realName == "") {
      Toast('请输入持卡人真实姓名');
      return;
    }
    if (this.bankName == "") {
      Toast('请填写开户银行名称');
      return;
    }
    if (this.cardId == "") {
      Toast('请填写银行卡号');
      return;
    }
    if (this.subBranch == "") {
      Toast('请填写银行卡所在支行');
      return;
    }
    Vue.prototype.$reqFormPost(
      "/bank/card/add",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cardId: this.cardId,
        bankName: this.bankName,
        realName: this.realName,
        subBranch: this.subBranch
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        this.getBankCardList();
        console.log(res.data);
        this.addshow=false;
      }
    );
  }
    getreward(){
    Vue.prototype.$reqFormPost("/user/account/query", {
      userId: this.$store.getters[
            Vue.prototype.MutationTreeType.TOKEN_INFO
        ].userId,
        token: this.$store.getters[
            Vue.prototype.MutationTreeType.TOKEN_INFO
        ].token,
     }, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message)
        return;
      }
      this.award = res.data.data;
      this.awardBalance=res.data.data.awardBalance;
      console.log("award",res.data.data);
    });
  }
  addrecord(){
    if(this.amount==""){
      Toast('请输入提现金额');
      return
    }
    if(this.card_Id==""){
      Toast('请选择银行卡');
      return
    }
    if(this.amount>this.awardBalance){
      Toast('提现金额不能大于余额');
      return
    }
    Vue.prototype.$reqFormPost("/withdraw/record/add", {
      userId: this.$store.getters[
            Vue.prototype.MutationTreeType.TOKEN_INFO
        ].userId,
        token: this.$store.getters[
            Vue.prototype.MutationTreeType.TOKEN_INFO
        ].token,
        cardId:this.card_Id,
        amount:this.amount
     }, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message)
        return;
      }
      console.log("addrecord",res.data);
      Toast('提现成功');
      this.getreward();      
      this.isextractshow=false;
    });
  }
  get_user_rewardlist(){
    Vue.prototype.$reqFormPost("/award/record/userquery", {
      userId: this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
      ].userId,
      token: this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
      ].token,
      // page:this.pageindex,
      // pageSize:20
     }, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message)
        return;
      }
      // let awardList = this.awardList ? this.awardList : [];
      //   for (let i = 0; i < res.data.data.awardList.length; i++) {
      //     awardList.push(res.data.data.awardList[i]);
      //   }
      //   if (res.data.data.awardList.length == 20) {
      //     this.loading = false;
      //   }
      this.awardList = res.data.data.awardList;
      console.log("awardList",res.data.data.awardList);
    });
  }
    gogetreward(){
        this.$router.push("/reward");
    }
    gorewarddetail(){
    this.$router.push("/present_record");
  }
  gomember(){
    this.$router.push("/my_member");
  }
    handlePX(CssName, PxNumber) {
    return CssName +":" +this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /750 * PxNumber +"px;";
  }
  loadMore() {
    console.log("刷新");
    this.loading = true;
    let self = this;
    setTimeout(() => {
      self.pageindex = self.pageindex + 1;
      self.get_user_rewardlist();
      self.loading = false;
    }, 1000);
  }
  mounted() {
    this.getreward();
    this.get_user_rewardlist();
    this.getdefaultbankcard();
    console.log("我的奖励");
    
    this.$emit('selectMenu',{
      name: '我的积分',
      url:'/my_reward',
    })
  }
}



